import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { BeneficiariesService } from 'src/app/commonBussinessSRC/psServices/beneficiaries/beneficiaries.service';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { IOmniBeneficiaryRequest } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IMapKeyValue } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsCashTransferComponent } from '../../ps-beneficiary/ps-cash-transfer/ps-cash-transfer.component';
import { IOptionsPsComplexLookUpComponentExposed } from '../../ps-complex-components/ps-complex-lookup/ps-complex-lookup.component.interfaces';
import { IOptionsPsLookupBenificiariesCashExposed } from './ps-lookup-beneficiaries-cash.component.interfaces';


/**
 * @author G.Radwan
 * @since 05/06/2021
 *
 * <p> PsLookupBeneficiariesCashComponent is a lookup component to show local benificiaries list fetched from server</p>
 */
@Component({
  selector: 'ps-lookup-beneficiaries-cash',
  templateUrl: './ps-lookup-beneficiaries-cash.component.html',
  styleUrls: ['./ps-lookup-beneficiaries-cash.component.scss'],
})
export class PsLookupBeneficiariesCashComponent extends PsBaseFieldComponent implements OnInit {

  public formGroup: FormGroup = new FormGroup({});
  @Input() options: IOptionsPsLookupBenificiariesCashExposed = {
    requestObject: null
  };
  public complexLookupOptions: IOptionsPsComplexLookUpComponentExposed = {
    requestObject: null,
    notFoundMessage: 'no_cash_beneficiary_key'
  };
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() public onBenificiaryChange: EventEmitter<any> = new EventEmitter<any>();
  public listOfBenificiaries?: any[];
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public beneficiariesRequestParam: IOmniBeneficiaryRequest = {};
  private cashBeneficiariesList: any[];

  constructor(
    public commonService: PsCommonService,
    logger: LoggerService,
    private omniPull?: OmniPullService,
    private beneficiariesService?: BeneficiariesService
  ) {
    super(commonService, logger);
    this.populateCashBenificiaryMap();
  }

  ngOnInit() {
    super.init();
    this.loadCashBenificiaries();
    this.complexLookupOptions.title = 'title_key';
    this.complexLookupOptions.subTitle = 'sub_title_key';
    this.complexLookupOptions.labelsValueMap = this.labelValuesMap;
    this.commonProv.copyObject(this.complexLookupOptions, this.options, false);// Moved here by Richie for #BUG 1141192
    this.complexLookupOptions.requestObject = this.options.requestObject;
    if (this.options && this.options.component) {
      this.complexLookupOptions.component = this.options.component;
    } else {
      this.complexLookupOptions.component = PsCashTransferComponent;
    }
  }

  /**
   * populating Local Benificiaries - fetching from server
   */
  private loadCashBenificiaries() {
    this.options.listOfBenificiaries = [];
    this.beneficiariesRequestParam = {
      trxTypeParameterName: CommonBussinessConstant.CASH_TRANSFER,
      filterByOper: true
    };
    this.returnCashBenificiaries(this.beneficiariesRequestParam);
  }

  /**
   * fetching Local Benificiaries from server
   * @param requestData IOmniBeneficiaryRequest
   */
  private async returnCashBenificiaries(requestData: IOmniBeneficiaryRequest) {
    const result = await this.omniPull.returnBeneficiaries(requestData).catch(error => {
      this.complexLookupOptions.listOfOptions = [];
      this.logger.error('Error: While fetching cash beneficiaries in PsLookupBeneficiariesCashComponent :', error);
    });

    if (result && result.gridModel.length > 0) {
      this.complexLookupOptions.listOfOptions = this.populateCustomCashBeneficiaries(result.gridModel);
    } else {
      this.listOfBenificiaries = [];
      this.complexLookupOptions.listOfOptions = [];
    }
  }


  private populateCashBenificiaryMap() {
    let beneficiaryIdNumberMap: IMapKeyValue = {};
    let beneficiaryNameMap: IMapKeyValue = {};

    beneficiaryIdNumberMap = {
      key: 'idNumber',
      value: '--- --- ---',
      isEdit: false
    };

    beneficiaryNameMap = {
      key: 'benefName',
      value: '------',
      isEdit: false
    };


    // key here will be used as label in look up details component
    this.labelValuesMap.set('beneficiary_key', beneficiaryNameMap);
    this.labelValuesMap.set('bank_name_key', beneficiaryIdNumberMap);
  }

  onChangeItem(event) {
    this.onBenificiaryChange.emit(event);
  }

  /**
   * customizing local beneficiary object
   * @param gridModel
   */
  //modified by ghada, AZDB-309 for identifying and distinguishing each beneficiary's related keys, to fix the issue of keys with similar name getting affected by a beneficiary customization
  private populateCustomCashBeneficiaries(localBeneficiaries: Array<any>): IOmniBeneficiaryRequest[] {
    return this.beneficiariesService.populateCashTransfers(localBeneficiaries);
  }


}

