import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { BeneficiariesService } from 'src/app/commonBussinessSRC/psServices/beneficiaries/beneficiaries.service';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { IOmniBeneficiaryRequest } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { IMapKeyValue } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { PsInternalBeneficiaryComponent } from '../../ps-beneficiary/ps-internal-beneficiary/ps-internal-beneficiary.component';
import { IOptionsPsComplexLookUpComponentExposed } from '../../ps-complex-components/ps-complex-lookup/ps-complex-lookup.component.interfaces';
import { IOptionsPsLookupBenificiariesLocalCardExposed } from './ps-lookup-beneficiaries-local-card.component.interface';



@Component({
  selector: 'ps-lookup-beneficiaries-local-card',
  templateUrl: './ps-lookup-beneficiaries-local-card.component.html',
  styleUrls: ['./ps-lookup-beneficiaries-local-card.component.scss'],
})
export class PsLookupBeneficiariesLocalCardComponent extends PsBaseFieldComponent implements OnInit {

  public formGroup: FormGroup = new FormGroup({});
  @Input() options: IOptionsPsLookupBenificiariesLocalCardExposed = {
    requestObject: null
  };
  @ViewChild('complexLookupComponentObj') complexLookupComponentObj;
  public complexLookupOptions: IOptionsPsComplexLookUpComponentExposed = {
    requestObject: null,
    notFoundMessage: 'no_local_card_beneficiary_key'
  };
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() public onBenificiaryChange: EventEmitter<any> = new EventEmitter<any>();
  public listOfBenificiaries?: any[];
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public beneficiariesRequestParam: IOmniBeneficiaryRequest = {};
  private localCardBeneficiariesList: any[];

  constructor(
    public commonService: PsCommonService,
    logger: LoggerService,
    private omniPull?: OmniPullService,
    private beneficiariesService?: BeneficiariesService
  ) {
    super(commonService, logger);
    this.populateLocalCardBenificiaryMap();
  }

  ngOnInit() {
    super.init();
    this.loadLocalCardBenificiaries();
    this.complexLookupOptions.title = 'title_key';
    this.complexLookupOptions.subTitle = 'sub_title_key';
    this.complexLookupOptions.labelsValueMap = this.labelValuesMap;
    this.commonProv.copyObject(this.complexLookupOptions, this.options, false);
    this.complexLookupOptions.requestObject = this.options.requestObject;
    if (this.options && this.options.component) {
      this.complexLookupOptions.component = this.options.component;
    } else {
      this.complexLookupOptions.component = PsInternalBeneficiaryComponent;
    }
  }

  /**
   * populating Local Card Benificiaries - fetching from server
   */
  private loadLocalCardBenificiaries() {
    this.options.listOfBenificiaries = [];
    this.beneficiariesRequestParam = {
      trxTypeParameterName: CommonBussinessConstant.LOCAL_CARD_BENEFICIARY_TYPE,
      filterByOper: true
    };
    this.returnLocalCardBenificiaries(this.beneficiariesRequestParam);
  }

  /**
   * fetching Local Card Benificiaries from server
   * @param requestData IOmniBeneficiaryRequest
   */
  private async returnLocalCardBenificiaries(requestData: IOmniBeneficiaryRequest) {
    const result = await this.omniPull.returnBeneficiaries(requestData).catch(error => {
      this.complexLookupOptions.listOfOptions = [];
      this.logger.error('Error: While fetching local card beneficiaries in PsLookupBeneficiariesLocalCardComponent :', error);
    });

    if (result && result.gridModel.length > 0) {
      this.complexLookupOptions.listOfOptions = this.populateCustomLocalCardBeneficiaries(result.gridModel);
    } else {
      this.listOfBenificiaries = [];
      this.complexLookupOptions.listOfOptions = [];
    }
  }


  private populateLocalCardBenificiaryMap() {
    let beneficiaryCardNumberMap: IMapKeyValue = {};
    let beneficiaryLocalNameMap: IMapKeyValue = {};

    beneficiaryCardNumberMap = {
      key: 'beneficiaryCardNumber',
      value: '--- --- ---',
      isEdit: false
    };

    beneficiaryLocalNameMap = {
      key: 'beneficiaryLocalName',
      value: '------',
      isEdit: false
    };


    // key here will be used as label in look up details component
    this.labelValuesMap.set('beneficiary_key', beneficiaryCardNumberMap);
    this.labelValuesMap.set('local_name_key', beneficiaryLocalNameMap);
  }

  onChangeItem(event) {
    this.onBenificiaryChange.emit(event);
  }

  /**
   * customizing local card beneficiary object
   * @param gridModel
   */
  //modified by ghada, AZDB-309 for identifying and distinguishing each beneficiary's related keys, to fix the issue of keys with similar name getting affected by a beneficiary customization
  private populateCustomLocalCardBeneficiaries(localCardBeneficiaries: Array<any>): IOmniBeneficiaryRequest[] {
    return this.beneficiariesService.populateLocalCardBeneficiaries(localCardBeneficiaries);
  }

  reset() {
    this.complexLookupComponentObj.resetLookUp()
  }
}

